import React from 'react'
import './contact.css'
import{AiOutlineMail} from 'react-icons/ai'
import{FaFacebookMessenger} from 'react-icons/fa'
import{BsWhatsapp} from 'react-icons/bs'
import  { useRef } from 'react'
import emailjs from 'emailjs-com'


const Contact = () => {
  const form = useRef()

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_jtr5b0u', 'template_mgvbtqr', form.current, 'QM4zmwCYLQ3c29NlA')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });

      e.target.reset()
  };

  return (
   <section id='contact'>
     <h5 className='contacter'>Get in touch</h5>
     <h2>Contact Me</h2>
    
     <div className="container contact__container">
      <div className="contact__options">
        <article className='contact__option'>
        <AiOutlineMail className='contact__option-icon'/>
          <h4>Email</h4>
          <h5>echikaraphael@gmail.com</h5>
          <a href="mailto:echikaraphael@gmail.com" target='_blank'>Send a message</a>
        </article>

        <article className='contact__option'>
        <FaFacebookMessenger className='contact__option-icon'/>
          <h4>Messenger</h4>
          <h5>Beelraph tech</h5>
          <a href="https://m.me/echika.raphael" target='_blank'>Send a message</a>
        </article>

        <article className='contact__option'>
        <BsWhatsapp className='contact__option-icon'/>
          <h4>Whatsapp</h4>
          <h5>Whatsapp Messenger</h5>
          <a href="https://api.whatsapp.com/send?phone=+2347085706889" target='_blank'>Send a message</a>
        </article>
      </div>
     

     <form ref={form} onSubmit={sendEmail}>
      <input type="text" name='name' placeholder='Full Name' required/>
      <input type="email" name='email' placeholder='Email' required/>
      <textarea name="message"  rows="8" placeholder='Enter Your Message' required></textarea>

      <button className='btn btn-primary' type='submit'>Send Message</button>
     </form>
     </div>
   </section>
  )
}

export default Contact