import React from 'react'
import './services.css'
import {AiOutlineCheck} from 'react-icons/ai'

const Services = () => {
  return (
  <section id='services'>
   <h5 className='skills'>What I Offer</h5>
   <h2>Services</h2>
   <div className="container services__container">
   

    <article className='service'>
     <div className="service__head">
      <h3>Web Development</h3>
     </div>
     <ul className="service__list">
      <li><AiOutlineCheck className='service__list-icon'/><p>Strong experience in software development and mobile application.</p></li>
      <li><AiOutlineCheck className='service__list-icon'/><p>Experience with popular React.js workflow.</p></li>
      <li><AiOutlineCheck className='service__list-icon'/><p>A strong understanding of HTML, CSS, JavaScript and React JS.</p></li>
      <li><AiOutlineCheck className='service__list-icon'/><p>Skilled at working with teams and leading teams.</p></li>
      
     </ul> 
    </article>

    <article className='service'>
     <div className="service__head">
      <h3>Mentoring</h3>
     </div>
     <ul className="service__list">
      <li><AiOutlineCheck className='service__list-icon'/><p>Provide designs to practice front end skills  using real-life workflow</p></li>
      <li><AiOutlineCheck className='service__list-icon'/><p>Helped and still helping upcoming web developers understand core concepts and follow best practices</p></li>
      <li><AiOutlineCheck className='service__list-icon'/><p>Give students projects to help hone their front end development skills</p></li>
      
     </ul>
    </article>
   </div>
  </section>
  )
}

export default Services