import React from "react";
import "./portfolio.css";

import IMG1 from "../../pictures/calc.jpg";
import IMG2 from "../../pictures/todoapp.jpeg";
import IMG3 from "../../pictures/fashionweb.jpeg";
import IMG4 from "../../pictures/agecal.jpeg";

const data = [
  {
    id: 1,
    image:  IMG1,
    title: "Basic Calculator",
    demo: "https://echika.github.io/calc",
    github: "https://github.com/Echika/calculator",
  },

  {
    id: 2,
    image: IMG2,
    title: "A Basic Todo App",
    demo: "https://echika.github.io/todo-app",
    github: "https://github.com/Echika/todo-app",
  },

  {
    id: 3,
    image: IMG3,
    title: "A Fashion Website",
    demo: "https://echika.github.io/fashion",
    github: "https://github.com/Echika/fashion",
  },

  {
    id: 4,
    image: IMG4,
    title: "An Agecalculator",
    demo: "https://echika.github.io/agecalc",
    github: "https://github.com/Echika/agecalc",
  }

];

const Portfolio = () => {
  return (
    <section id="portfolio">
      <h5 className="workportfolio">My Recent Works</h5>
      <h2>Portfolio</h2>

      <div className="container portfolio__container">
       {
        data.map(({id, image, title, demo, github}) =>{
        return(
          <article key={id} className="portfolio__item">
          <div className="portfolio__item-image">
            <img src={image} alt={title} />
            <h3>{title}</h3>
            <div className="portfolio__item-cta">
              <a
                href={github}
                className="btn"
                target="_blank"
              >
                Github
              </a>
              <a
                href={demo}
                className="btn btn- primary"
                target="_blank"
              >
                Preview
              </a>
            </div>
          </div>
        </article>
        
        )
})
       }
      </div>
    </section>
  );
};

export default Portfolio;
