import React from 'react'
import './about.css'
import ME from '../../pictures/ogom.jpg'
import {RiAwardFill} from 'react-icons/ri'
import {FaUserSecret} from 'react-icons/fa'
import {AiTwotoneFolder} from 'react-icons/ai'

const About = () => {
  return (
    <section id='about'>
     <h5 className='gettoknow'>Get to know</h5>
     <h2>About me</h2>

     <div className="container about__container">
      <div className="about__me">
        <div className="about__me-image">
          <img src={ME} alt="" />
        </div>
      </div>

      <div className="about__content">
      <div className="about__cards">
        <article className='about__card'>
        <RiAwardFill className='about__icon'/>
           <h5>Experience</h5>
           <small>1+ years working experience</small>
           
        </article>
        <article className='about__card'>
        <FaUserSecret className='about__icon'/>
           <h5>Clients</h5>
           <small>20+ Clients Worldwide</small>
           
        </article>
        <article className='about__card'>
        <AiTwotoneFolder className='about__icon'/>
           <h5>Projects</h5>
           <small>50+ completed projects</small>
           
        </article>
      </div>

      <p>
I'm a meticulous and passionate, certified Web developer with almost 2 years of front end experience.
I apply myself to the best practices and believe so much in the mobile first approach to website development.


      </p>

      <a href="#contact" className='btn btn-primary' id='letstalk'>Let's talk</a>
      </div>
     </div>
    </section>
  )
}

export default About